body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    height: auto;
    min-height: 100%;
  }
  
  .header {
    background-color: #4da6ff;
    padding: 20px;
    text-align: center;
  }
  
  .header-title {
    font-size: 40px;
    color: #fff;
    margin: 0;
  }
  
  .header-nav {
    margin-top: 10px;
  }
  
  .nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .nav-list li {
    display: inline;
    margin-right: 20px;
  }
  
  .nav-list li a {
    color: #fff;
    text-decoration: none;
    font-size: 1.2em;
  }
  
  .nav-list li a:hover {
    text-decoration: underline;
  }
  
  .main-content {
    padding: 20px;
    text-align: center;
    height: auto;
    min-height: 100%;
  }
  
  .section-title {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .section-description {
    color: #666;
    margin-bottom: 20px;
  }
  
  .team-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .team-table th, .team-table td {
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
  }
  
  .team-table th {
    background-color: #4da6ff;
    color: #fff;
  }
  
  .image-carousel {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .image-carousel img {
    max-width: 100%;
    height: auto;
    margin: 0 10px;
  }
  
  .video-player {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .footer {
    background-color: #4da6ff;
    color: #ffffff;
    text-align: center;
    padding: 20px;
    height: auto;
    min-height: 100%;
  }
  